export default {
	data() {
		return {
			list:[
				{
					name:'李小吧',
					age:'22',
					tag:['电工学徒'],
					address:'深圳市龙华区大浪街',
					status:'待审核',
					status_no:2,
				},
				{
					name:'李小吧',
					age:'22',
					tag:['电工学徒'],
					address:'深圳市龙华区大浪街',
					status:'已录用',
					status_no:1,
				},
				{
					name:'李小吧',
					age:'22',
					tag:['电工学徒'],
					address:'深圳市龙华区大浪街',
					status:'已拒绝',
					status_no:0,
				},
			],
			show_modal:false,
			recruiting_info:{},
			recruit_list:[],
			requirementInfo_list:[]
		};
	},
	props: {
		route_id: {
			type: String,
			default() {
				return '';
			}
		},
		task_id: {
			type: String,
			default() {
				return '';
			}
		},
	},
	onShareAppMessage(res) {
		console.log(res)
	    if (res.from === 'button') {// 来自页面内分享按钮
	      console.log(res.target)
	    }
	    return {
	      title: '快来加入我们',
	      path: 'pages/tabbar/task/task',
		  imageUrl:'@/static/no-team.png'
	    }
	},
	computed: {
		
	},
	mounted() {
		
		uni.$on('refreshzmList',() =>{
			this.init()
		})
		uni.$on('refreshJwt',(data) =>{
			this.init()
		})
		this.init()
	},
	beforeDestroy() {
		console.log('beforeDestroy')
		uni.$off('refreshzmList')
		uni.$off('refreshJwt')
	},
	methods: {
		init(){
			this.get_recruiting_info()
		},
		get_recruiting_info(){
			this.$http.get('personwx.projectrecruitActive/1.0/',{
				status:0,
				proId:this.route_id?this.route_id:this.task_id
			}).then(res =>{
				console.log(res)
				this.recruiting_info = res.data.data[res.data.data.length-1]
				// console.log(JSON.parse(this.recruiting_info.requirementInfo))
				this.get_recruit_list(this.recruiting_info.id)
				this.$emit('update_recruiting_info',this.recruiting_info)
				try{
					this.requirementInfo_list = JSON.parse(this.$utils._get(this.recruiting_info,'requirementInfo','[]'))
					console.log(this.requirementInfo_list )
				}catch(e){
					//TODO handle the exception
				}
				
			})
		},
		goto_detail(item){
			
			uni.navigateTo({
				url: `/pages/auditDetail/auditDetail?id=${item.id}&pid=${item.pid}` 
			});
		},
		get_recruit_list(recid){
			this.$http.get('personwx.listrecruitperson/1.0/',{
				recId:recid
			}).then(res =>{
				console.log('get_recruit_list',res)
				this.recruit_list = res.data.data
			})
		},
		status_class(status_no){
			switch (status_no){
				case '1':
					return 'bg-gradual-blue'
					break;
				case '2':
					return 'bg-gradual-green'
					break;
				case '3':
					return 'bg-gradual-red'
					break;
				case '4':
					return 'bg-gradual-purple'
					break;
				case '5':
					return 'bg-gradual-pink'
					break;
				default:
					return ''
					break;
			}
		},
		status_text(status_no){
			//状态 1：待回复2：已同意，3已拒绝，4：待审核,5录用，6、不录用
			switch (status_no){
				case '1':
					return '待回复'
					break;
				case '2':
					return '已同意'
					break;
				case '3':
					return '已拒绝'
					break;
				case '4':
					return '待审核'
					break;
				case '5':
					return '录用'
					break;
				case '6':
					return '不录用'
					break;
				default:
					return ''
					break;
			}
		},
		goto_recruiting(){
			uni.navigateTo({
				url: `/pages/personList/personList?recId=${this.recruiting_info.id}`
			});
		},
		show_p_modal(){
			this.$emit('show_modal')
		},
		avatarUrl(avatarUrl){
			return `url(${avatarUrl})`
		},
		current_zm_num(type){
			let num = 0
			this.recruit_list.forEach(item =>{
				if(item.status === '5'){
					num++
				}
			})
			return num
		}
	}
};
